header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;

    background-color: #202020;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    user-select: none;
    z-index: 10000;

    user-select: none;
    
    padding: 0 80px;
}

nav ul a {
    color: #8B8B8B;
    position: relative;
    margin-right: 50px;
    text-shadow: none;

    transition: all .3s;
}

header nav ul a::after {
    content: "";
    position: absolute;
    background-color: #F0F0F0;
    height: 2px;
    width: 0;
    left: 0;
    bottom: -3px;
    transition: .3s;
}

header nav ul a.active {
    color: #F0F0F0;
}

header nav ul a:hover {
    color: #F0F0F0;
}

header nav ul a:hover::after {
    width: 100%;
}

.toggle_btn {
    cursor: pointer;
    color: #F0F0F0;
    display: none;
}

.dropdown-menu {
    position: absolute;
    width: 350px;
    border-radius: 4px;
    overflow: hidden;   
    display: none;
    background-color: #232323a2;
    height: 0;
    transition: height .3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

header .dropdown-menu {
    right: 40px;
    top: 80px;
}

.dropdown-menu ul {
    display: flex;
    flex-direction: column;
}

.dropdown-menu a {
    padding: 20px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    color: #F0F0F0;
}

.dropdown-menu a:hover {
    background-color: #8484840b;
}

.dropdown-menu.open {
    border: 1px solid rgb(53, 53, 53);
    height: 140px;
}

@media (max-width: 1000px) {
    header {
        padding: 20px;
    }
    
    .desktop-nav {
        display: none;
    }

    .toggle_btn,
    .dropdown-menu {
        display: block;
    }
}

@media (max-width: 600px) {
    .dropdown-menu.open {
        left: 20px;
        right: 20px;
        width: unset;
    }
}