#about {
    padding-bottom: 100px;
}

.main-about {
    width: 80%;
}

.intro-container {
    height: 100vh;
}

h2 {
    font-size: 60px;
    line-height: 60px;

    margin-bottom: 70px;
}

.intro-container p {
    font-size: 20px;
}

.sub-container {
    justify-content: space-evenly;
    margin-bottom: 80px;
}

.mobile-info {
    display: none;
}

.img-container {
    height: 300px;
    width: 300px;
    margin: 0 20px;
    border-radius: 6px;
    box-shadow: rgba(195, 196, 197, 0.37) 0px 8px 24px;
}

.img-container img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 6px;
}

.info-con {
    width: 500px;
    margin: 0 20px;
}

h3 {
    font-size: 24px;
    margin-bottom: 12px;
}

@media (max-width: 1000px) {
    h2 {
        font-size: 50px;
    }

    .intro-container p {
        font-size: 18px;
    }

    .sub-container {
        flex-direction: column;
        margin-bottom: 100px;
        width: 100%;
    }

    .mobile-info {
        display: block;
    }

    .info-con {
        width: 100%;
    }

    .desktop-info {
        display: none;
    }

    .img-container {
        margin-bottom: 50px;
    }
}

@media (max-width: 600px) {
    .intro-container {
        margin-top: 50px;
        margin-bottom: 50px;
        height: 700px;
    }
}

@media (max-width: 500px) {
    h2 {
        font-size: 40px;
    }
}

@media (max-width: 380px) {
    .intro-container {
        margin-top: 20px;
        margin-bottom: 40px;
        height: 900px;
    }

    h2 {
        line-height: 40px;
        font-size: 30px;
    }

    .img-container {
        height: 240px;
        width: 240px;
    }
}


