#home {
    height: 100vh;
    padding-top: 50px;
}

.main-home {
    width: 80%;
}

h1 {
    font-size: 110px;
    letter-spacing: 6px;
    line-height: 130px;

    margin-bottom: 80px;
}

.no-background {
    background-color: transparent;
    border: 2px solid #F0F0F0;
    border-radius: 4px;
    font-size: 20px;
    padding: 6px 26px;
    cursor: pointer;

    user-select: none;

    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

    transition: all .3s;
}

.no-background:hover {
    background-color: #F0F0F0;
    color: #0D0D0D;
}

.no-background:active {
    background-color: #bdbdbd;
    border: 2px solid #bdbdbd;
    scale: .98;
    color: #0D0D0D;
}

@media (max-width: 1300px) {
    h1 {
        font-size: 100px;
    }
}

@media (max-width: 1000px) {
    h1 {
        font-size: 90px;
    }
}

@media (max-width: 800px) {
    h1 {
        font-size: 70px;
        letter-spacing: 4px;
        line-height: 80px;

        margin-bottom: 60px;
    }

    .no-background {
        font-size: 14px;
    }
}

@media (max-width: 600px) {
    .main-home {
        width: 80%;
    }

    h1 {
        font-size: 60px;
        letter-spacing: 3px;
        line-height: 60px;

        margin-bottom: 50px;
    }

    .no-background {
        font-size: 14px;
    }
}

@media (max-width: 500px) {
    h1 {
        font-size: 40px;
        letter-spacing: 3px;
        line-height: 60px;

        margin-bottom: 50px;
    }
}

@media (max-width: 380px) {
    h1 {
        font-size: 35px;
        letter-spacing: 2px;
        line-height: 40px;

        margin-bottom: 30px;
    }
}