footer {
    background-color: #F0F0F0;
    height: 200px;

    padding: 140px 100px;
}

.footer-top {
    justify-content: space-between;
    width: 100%;
    margin-bottom: 40px;
    user-select: none;
    padding: 0 100px;
}

.footer-top nav ul a.margin-left {
    margin: 0;
}

.footer-top a {
    color: #202020;
    font-weight: 600;
}

hr {
    border-bottom: 2px solid #a7a7a78b;
    width: 85%;
}

.icons-main {
    margin-top: 40px;
}

footer a i {
    color: #202020;
}

footer .icons {
    border: 2px solid #202020;
    padding: 8px;
    border-radius: 50%;
    margin: 0 8px;
    transition: background-color .3s;
}

footer .icons:hover {
    background-color: #202020;
}

footer .icons:active {
    scale: .97;
}

footer .icons i {
    transition: color .3s;
}

footer .icons:hover i {
    color: #F0F0F0;
}

.icons-main p {
    color: #202020;
    font-weight: 400;
    font-size: 13px;
    margin-top: 12px;
}

.icons-main p.dev-name {
    margin-top: 0;
}

.icons-main p span a {
    font-weight: 600;
    color: #202020;
}

.icons-main p span a:hover {
    color: #3a3c43;
}

@media (max-width: 1550px) {
    hr {
        width: 80%;
    }
}

@media (max-width: 1300px) {
    footer {
        padding: 140px 70px;
    }

    hr {
        width: 70%;
    }
}

@media (max-width: 1000px) {
    footer {
        padding: 140px 30px;
    }

    hr {
        width: 70%;
    }
}

@media (max-width: 800px) {
    footer {
        padding: 130px 10px;
    }
    
    .footer-top {
        width: 100%;
        margin-bottom: 20px;
        padding: 0 65px;
    }

    .icons-main {
        margin-top: 20px;
    }
}

@media (max-width: 500px) {
    footer {
        padding: 110px 0px;
    }
    
    .footer-top {
        width: 100%;
        margin-bottom: 20px;
        padding: 0 40px;
    }

    .icons-main {
        margin-top: 20px;
    }

    .footer-top nav ul a {
        margin: 10px;
    }
}


@media (max-width: 380px) {
    .footer-top {
        display: none;
    }

    footer {
        padding-top: 90px;
    }

    hr {
        display: none;
    }
}

